    width: 100px;
    display: inline-block;
}
#nav ul {
    margin: 0;
    list-style-type: none;
    overflow: hidden;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0;
}
#nav li {
    float: left;
}
#nav li a {
      display: block;
      color: white;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none;
}
#nav li a:hover {
        color: gray;
}

